.cmp-header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 9999;
    display: flex;
    align-items: center;
    padding: 16px 24px;
    background-color: #fffaf0;
    border-bottom: 1px solid #e5d5c2;
    font-family: 'Playfair Display', serif;
    justify-content: center;
  }
  
  .hamburger {
    position: absolute;
    left: 10px;
    font-size: 28px;
    background: none;
    border: none;
    cursor: pointer;
    color: var(--secondary-color);
  }
  
  .header-title {
    font-size: 24px;
    color: var(--secondary-color);
    margin: 0;
    display: inline-flex;
    gap: 10px;
  }
  
  .nav-menu {
    position: absolute;
    top: 60px;
    left: 0;
    background-color: rgba(255, 255, 255, 0.9);
    border: 1px solid #d2b48c;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 8px;
    z-index: 1000;
  }
  
  .nav-menu button {
    background: none;
    border: solid 1px var(--secondary-color);
    font-size: 24px;
    padding: 8px 12px;
    text-align: left;
    color: var(--secondary-color);
    font-family: 'Cormorant Garamond', serif;
    cursor: pointer;
  }
  
  .nav-menu button:hover {
    background-color: #f5e4d0;
    border-radius: 4px;
  }
  